//loading
export const ROLE_LOADING_STATE = 'ROLE_LOADING_STATE'
export function roleLoadingChange(result) {
    return {
        type: ROLE_LOADING_STATE,
        result
    }
}
//角色列表开始请求
export const ROLE_TABLE_LIST_FETCH = "ROLE_TABLE_LIST_FETCH"
export function roleTalbeListFetch(result) {
    return {
        type: ROLE_TABLE_LIST_FETCH,
        result
    }
}
//角色列表装填数据
export const ROLE_TABLE_LIST = "ROLE_TABLE_LIST"
export function roleTalbeList(result) {
    return {
        type: ROLE_TABLE_LIST,
        result
    }
}
//角色列表搜索
export const ROLE_TABLE_LIST_SEARCH = "ROLE_TABLE_LIST_SEARCH"
export function roleTalbeListSearch(result) {
    return {
        type: ROLE_TABLE_LIST_SEARCH,
        result
    }
}

//角色 修改 和 新增
export const ROLE_INSERT_AND_UPDATE = "ROLE_INSERT_AND_UPDATE"
export function roleUpdateInsert(result, callback) {
    return {
        type: ROLE_INSERT_AND_UPDATE,
        result,
        callback
    }
}

//删除角色
export const ROLE_DELETE = "ROLE_DELETE"
export function deleteRole(result, callback) {
    return {
        type: ROLE_DELETE,
        result,
        callback
    }
}


//编辑角色获取权限树
export const ROLE_EDIT_TREE_ID = "ROLE_EDIT_TREE_ID"
export function editRoleTreeId(id, callback) {
    return {
        type: ROLE_EDIT_TREE_ID,
        id,
        callback
    }
}

export const ROLE_EDIT_TREE = "ROLE_EDIT_TREE"
export function editRoleTree(params) {
    return {
        type: ROLE_EDIT_TREE,
        checked: params.checked,
        all: params.all,
        rootTree: params.rootTree
    }
}
//查看角色详情
export const ROLE_DETAIL_VIEW = "ROLE_DETAIL_VIEW"
export function viewRoleDetail(id, roles, users, loading) {
    return {
        type: ROLE_DETAIL_VIEW,
        id,
        roles,
        users,
        loading
    }
}

//菜单
export const MENU_TREE_LIST_ADD = "MENU_TREE_LIST_ADD"
export function menuTreeListAdd(callback) {
    return {
        type: MENU_TREE_LIST_ADD,
        callback
    }
}
export const MENU_TREE_LIST = "MENU_TREE_LIST"
export function menuTreeList(result) {
    return {
        type: MENU_TREE_LIST,
        result
    }
}
//菜单改变勾选状态后的数据
export const MENU_TREE_UPDATE = "MENU_TREE_UPDATE"
export function menuTreeUpdate(index, result) {
    return {
        type: MENU_TREE_UPDATE,
        index,
        result
    }
}
//保存按钮 loading 状态
export const MENU_TREE_SAVE_LOADING = "MENU_TREE_SAVE_LOADING"
export function menuSaveLoading(result) {
    return {
        type: MENU_TREE_SAVE_LOADING,
        result
    }
}


